<template>
  <div>
    <div v-for="item in currentList" :key="item.id" class="m-list-item">
      <div class="m-list-item-img-wrap">
        <img v-lazy="item.avatar" alt="" class="m-list-item-img">
      </div>
      <div class="m-list-item-info">
        <div>{{item.title}}</div>
        <div>
          <button @click="handleAdd(item)">收藏</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentList() {
      return this.$store.state.currentList
    }
  },
  methods: {
    handleAdd(item) {
      let myBook = this.$store.state.myBook
      myBook.push(item)
      this.$store.dispatch({ type: 'updateMyBook', myBook })
    }
  }
}
</script>

